@media (min-width: 768px) {
  .navbar-nav{
    font-size: 14px;
  }
  .navbar-collapse{
    width: 100% !important;
    margin: 0 !important;
  }
  .navbar{
    .navbar-brand>img {
      margin-left: 25px;
    }
  }
  header.sticky .navbar-header {
    width: 100%;
  }
  header.sticky .navbar-brand img {
    padding-top: 0px;
  }
  header.sticky .navbar-right {
    width: 80% !important;
    background: none;
    .container{
      margin-right: 25px;
    }
  }
  header.sticky .navbar-nav {
    float: left;
  }
  .navbar-default .social {
	font-size: 16px;
	margin-top: 20px;
  }
  .navbar-default .social > li {
    padding-left: 15px;
  }
}
