@font-face {
  font-family: "MetaWebPro Normal";
  src: url("../fonts/MetaWebPro-Normal.eot");
  src: url("../fonts/MetaWebPro-Normal.eot?#iefix") format("embedded-opentype"), url("../fonts/MetaWebPro-Normal.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
body {
     line-height: 1;
     background-color: #f5f5f5;
     .loader{
       .loader-image{
         background:url(../images/loader.gif) no-repeat center center #001e31 ;
         width:100%;
         height:100%;
         background-size: 15%;
       }
        // width:100%;
        // height:100%;
        // position:absolute;
        opacity:1;
        overflow: hidden;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1050;
        -webkit-overflow-scrolling: touch;
        outline: 0;
     }
}
ol, ul {
     list-style: none;
}

header{
    text-align: center;
    font-size: 72px;
    background: #222;
    color: #fff;
    font-family: Helvetica, Arial, sans-serif;
    font-weight: 100 !important;
    text-transform: capitalize;
    font-size: 32px;
    margin-bottom: 20px;
    margin-bottom: 20px;
    transition: all 500ms cubic-bezier(0.55, 0, 0.1, 1);
}
.navbar-default {
  background: #fff!important;
  border: 0px;
}
.navbar-header {
  width: 100%;
  margin-bottom: 35px;
}
.navbar-brand {
    padding:0px !important;
    margin-bottom: 5px;
}
.navbar-brand>img {
    display: block;
    margin: 0 auto;
    float: left;
    transition: all 500ms cubic-bezier(0.55, 0, 0.1, 1);
    max-width: 235px;
    margin-left: 85px;
    padding-top: 2px;
}
.navbar-right {
  width: 100%;
  background: #251f1f;
  margin-right: 0px;
}
// .navbar-collapse{
//   width: 100%;
//   margin: 0;
// }
.navbar-nav {
  float: left;
  font-size: 80%;
}
.navbar-default .navbar-nav>li {
  display: inline-block;
  text-align: center;
  float: none;
}
.navbar-default .navbar-nav>li>a {
  color: #fff !important;
}
.navbar-default .navbar-nav>li>a:hover {
  color: #fff !important;
  background-color: #0392CC;
}
.navbar-default .social {
  float: right;
  margin-top: 8px;
}
.navbar-default .social>li {
  display: inline-block;
  text-align: center;
  float: none;
  padding-left: 25px;
}
.navbar-default .social>li a {
  color: #fff;
}
.navbar-toggle {
  float: right;
  margin: 25px 0 -15px;
  transition: all 500ms cubic-bezier(0.55, 0, 0.1, 1);
}

header.sticky {
   position: fixed;
   font-size: 24px;
   line-height: 48px;
   height: 48px;
   width: 100%;
   background: #000000;
   text-align: left;
   padding-left: 20px;
   z-index: 20;
   top: 0;
}
header.sticky .container {
	width: inherit;
}
header.sticky .navbar-header {
  width: 20%;
  margin-bottom: 0px;
  float: left;
}
header.sticky .navbar-brand {
    height: auto;
    margin: 0;
    padding: 0;
}
header.sticky .navbar-brand img {
  padding-top: 5px;
  width: 130px;
  margin-right: 5px;
}
header.sticky .navbar-right {
  width: 80%;
  background: none;
}
header.sticky .navbar-nav {
  float: right;
  padding-top: 6px;
  li{
    a{
      color:#777 !important;
    }
  }
}
header.sticky .social {
  display: none;
}
header.sticky .navbar-toggle {
  float: right;
  margin-top: 7px;
}
.mainContainer{
    display: block;
    margin: 0 auto;
    text-align: center;
}
.carousel-container{
  .carousel-inner{
    .item{
      img{
        height: 600px;
        width: AUTO;
      }
      .carousel-caption{
        h2{
          background: rgba(70, 32, 39, 0.4);
          padding: 20px;
          margin-bottom: 75px;
          font-size: 32px;
        }
      }
    }
  }
}
.about-us{
  &.parallax{
    min-height: 200px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
  }
  h2{
    padding-bottom: 10px;
    text-align: center;
    display: inline-block;
    border-bottom: 1px solid #5a4a4a;
  }
  p{
    margin-bottom: 0px;
    text-align: center;
  }
  .key-features{
    img{
      height: 45px;
      vertical-align: top;
      margin-top: 20px;
    }
    ul{
      display: inline-block;
      padding-left: 5px;
      text-align: left;
      line-height: 28px;
    }
  }
}
.products{
  background-color: #251f1f;
  color: #fff;
  text-align: center;
  padding-bottom: 30px;
  h2{
    padding-bottom: 10px;
    text-align: center;
    display: inline-block;
    border-bottom: 1px solid #fff;
  }
  .sub-heading{
    font-size: 14px;
  }
  .product-container{
    display: table;
    margin: 0 auto;
    .products-item:first-child{
      padding-left: 20px;
    }
    .products-item{
      display: table-cell;
      width: 31.2%;
      background-color: #fff;
      border: 1px solid #415c12;
      h3{
        color:#000;
      }
      .desc-product{
        padding: 0 10px;
        p{
          text-align: center;
        }
      }
    }
    .space{
      width: 20px;
      display: table-cell;
    }
  }
}
.footer_top_holder{
  background-color: #251f1f;
  color:#fff;
  padding: 30px 0px;
  .footer_col2{
    float: left;
  }
  .footer_col3{
    float: left;
    margin-left: 20%;
  }
}
